// Variables

$toaster-default: #030303 !default;
$toaster-success: #51a351 !default;
$toaster-error: #bd362f !default;
$toaster-info: #2f96b4 !default;
$toaster-wait: #2f96b4 !default;
$toaster-warning: #f89406 !default;

$toaster-text-color: #ffffff !default;

$toaster-colors: () !default;
$toaster-colors: map-merge((
  "success": $toaster-success,
  "error": $toaster-error,
  "info": $toaster-info,
  "wait": $toaster-wait,
  "warning": $toaster-warning
), $toaster-colors);

$toaster-icon-success: "data:image/png;base64," !default;
$toaster-icon-error: "data:image/png;base64," !default;
$toaster-icon-info: "data:image/png;base64," !default;
$toaster-icon-wait: "data:image/gif;base64," !default;
$toaster-icon-warning: "data:image/png;base64," !default;

$toaster-icons: () !default;
$toaster-icons: map-merge((
  "success": $toaster-icon-success,
  "error": $toaster-icon-error,
  "info": $toaster-icon-info,
  "wait": $toaster-icon-wait,
  "warning": $toaster-icon-warning
), $toaster-icons);

$toaster-margin: 12px !default;

// Toast styles
.toast {
  display: flex;
  flex-direction: row;
  align-content: center;
  justify-content: center;
  position: relative;
  background-color: $toaster-default;
}

@each $color, $value in $toaster-colors {
  .toast-#{$color} {
    background-color: $value;
  }
}

@each $color, $value in $toaster-icons {
  .icon-#{$color} {
    background-repeat: no-repeat;
    background-image: url($value) !important;
  }
}

.toaster-icon {
  font-weight: normal;
  color: $toaster-text-color;
}

.toast-content {
  flex-grow: 1;
  padding: 15px 15px 15px 50px;
}

.toast-title {
  font-weight: bold;
}

.toast-message {
  word-wrap: break-word;

  & a,
  & label {
    color: $toaster-text-color;
  }

  & a:hover {
    color: #cccccc;
    text-decoration: none;
  }
} 

.toast-close-button {
  align-self: flex-start;
  padding: 3px;
  font-size: 23px;
  line-height: 90%;
  font-weight: bold;
  color: $toaster-text-color;
  text-shadow: 0 1px 0 $toaster-text-color;
  opacity: 0.7;
  z-index: 999;
  cursor: pointer;
  background: transparent;
  border: 0;

  &:hover,
  &:focus {
    color: #000000;
    text-decoration: none;
    cursor: pointer;
    opacity: 0.4;
    border: none;
  }

  span {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
}

.toast-progress-bar {
  position: absolute;
  left: 0;
  bottom: 0;
  height: 4px;
  background-color: #000000;
  opacity: 0.4;
}

/*Additional properties for button version
 iOS requires the button element instead of an anchor tag.
 If you want the anchor version, it requires `href="#"`.*/
button.toast-close-button {
  @extend .toast-close-button;
}

// Toast Container styles
.toast-container {
  position: fixed;
  z-index: 999999;
  /*overrides*/
  pointer-events: auto;
}
.toast-container.toast-center,
.toast-container.toast-top-center,
.toast-container.toast-bottom-center{
  width: 100%;
  pointer-events: none;
  left: 0;
  right: 0;
}
.toast-container.toast-center > div,
.toast-container.toast-top-center > div,
.toast-container.toast-bottom-center > div{
  margin: 6px auto;
  pointer-events: auto;
}
.toast-container.toast-center > button,
.toast-container.toast-top-center > button,
.toast-container.toast-bottom-center > button{
  pointer-events: auto;
}
.toast-container * {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.toast-container > div {
  margin: 0 0 6px;
  width: 300px;
  border-radius: 3px 3px 3px 3px;
  background-position: 15px center;
  background-repeat: no-repeat;
  box-shadow: 0 0 12px #999999;
  color: $toaster-text-color;
  opacity: 0.8;
}
.toast-container > :hover {
  box-shadow: 0 0 12px #000000;
  opacity: 1;
  cursor: pointer;
}

.toast-container.toast-top-full-width > div,
.toast-container.toast-bottom-full-width > div {
  width: 96%;
  margin: auto;
}

// Toast Container Positions
.toast-top-full-width {
  top: 0;
  right: 0;
  width: 100%;
}
.toast-bottom-full-width {
  bottom: 0;
  right: 0;
  width: 100%;
}
.toast-top-left {
  top: $toaster-margin;
  left: $toaster-margin;
}
.toast-top-center {
  top: $toaster-margin;
}
.toast-top-right {
  top: $toaster-margin;
  right: $toaster-margin;
}
.toast-bottom-right {
  right: $toaster-margin;
  bottom: $toaster-margin;
}
.toast-bottom-center {
  bottom: $toaster-margin;
}
.toast-bottom-left {
  bottom: $toaster-margin;
  left: $toaster-margin;
}
.toast-center {
  top: 45%;
}
